/*------------------------------------------------------------------
2.0 - Screenshot settings
------------------------------------------------------------------*/
#screenshotSettings {
  display: grid;
  grid-template-columns: repeat(2, 1fr); }
  #screenshotSettings #settingsSide input {
    width: 40px;
    text-align: center; }
  #screenshotSettings #settingsSide button#checkScreenshotConfig {
    margin-left: 15px; }
  #screenshotSettings #settingsSide p#checkScreenshotConfigDescription {
    margin: 10px 0px 0px 15px;
    color: grey;
    font-size: 14px;
    text-align: justify; }
  #screenshotSettings #imgCheckSide {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 50px; }
    #screenshotSettings #imgCheckSide #imgCheckContainer {
      display: flex;
      justify-content: center;
      align-items: center;
      min-width: 120px;
      width: 80%;
      max-width: 400px;
      height: 50%;
      border: 1px dashed grey;
      border-radius: 5px; }

/*------------------------------------------------------------------
3.0 - Normal settings
------------------------------------------------------------------*/
#normalSettings input#dataDirectory {
  width: 80%;
  max-width: 700px; }

#normalSettings button#browseDirectories {
  margin-left: 10px; }
  @media (max-width: 524px) {
    #normalSettings button#browseDirectories {
      margin-left: 0px;
      margin-top: 15px; } }

#normalSettings p#errIsNotDirectory {
  margin-top: 5px;
  color: #ff6d7e;
  font-size: 14px; }

#normalSettings #resetLogFileEntry {
  transition: all 0.2s ease-in-out; }

/* Switch Buttons style */
.switch {
  margin-top: 10px;
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px; }
  .switch input {
    opacity: 0;
    width: 0;
    height: 0; }
    .switch input:checked + .slider {
      background-color: #68b463; }
    .switch input:focus + .slider {
      box-shadow: 0 0 1px #68b463; }
    .switch input:checked + .slider:before {
      transform: translateX(20px); }
  .switch .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: grey;
    -webkit-transition: .4s;
    transition: .4s;
    /* Rounded sliders */ }
    .switch .slider:before {
      position: absolute;
      content: "";
      height: 16px;
      width: 16px;
      left: 2px;
      bottom: 2px;
      background-color: white;
      -webkit-transition: .4s;
      transition: .4s; }
    .switch .slider.round {
      border-radius: 34px; }
      .switch .slider.round:before {
        border-radius: 50%; }

/* End Switch Buttons style */
/*------------------------------------------------------------------
4.0 - Advanced settings
------------------------------------------------------------------*/
/*------------------------------------------------------------------
5.0 - Save & reset buttons
------------------------------------------------------------------*/
#buttons {
  margin-top: 30px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap; }
  @media (max-width: 504px) {
    #buttons {
      justify-content: space-between; } }
  #buttons button#saveConfig {
    background-color: #7289da;
    color: white;
    font-weight: lighter; }
  #buttons button#resetConfig {
    background-color: rgba(255, 255, 255, 0.5);
    color: black; }
    @media (max-width: 504px) {
      #buttons button#resetConfig {
        margin-top: 30px; } }

.primaryButtons,
.secondaryButtons {
  border-radius: 3px; }

.primaryButtons {
  width: 200px;
  height: 40px;
  font-size: 16px;
  transition: all .2s ease-in-out; }
  .primaryButtons:hover {
    transform: translateY(1.5px);
    box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.5); }

.secondaryButtons {
  padding: 5px 10px;
  font-size: 14px;
  background-color: rgba(255, 237, 114, 0.8);
  color: black; }

/*------------------------------------------------------------------
6.0 - Common properties
------------------------------------------------------------------*/
.settingsGroup {
  margin-bottom: 30px; }
  .settingsGroup label {
    color: white; }
  .settingsGroup input:not([type='color']) {
    margin-top: 5px;
    border-top: none;
    border-left: none;
    border-right: none;
    border-bottom: 1px solid grey;
    background: none;
    padding: 5px 0px;
    font-size: 15px;
    color: #ffed72; }
  .settingsGroup .entryGroup {
    margin: 0px 0px 20px 15px; }
    .settingsGroup .entryGroup span.comment {
      color: grey;
      font-size: 13px;
      margin-left: 5px; }

.disappear {
  transform: translateX(-20px);
  opacity: 0; }

.none {
  display: none; }
